import * as React from 'react';
import { Link } from 'react-router-dom';
import './Navigator.css';

interface INavigatorProps
{
    Contents: any[];
}

export class Navigator extends React.Component<INavigatorProps>
{
    constructor(props: INavigatorProps)
    {
        super(props);
    }

    render(): React.ReactNode
    {
        return <div className='Navigator'>
            {this.props.Contents?.map(g => 
                <ul className='NavigatorGroup'>
                    <li className='NavigatorGroupTitle'>
                        <Link to={'/' + g.Title} children={g.Title}/>
                    </li>
                    {g.Galleries?.map((c: string) =>
                        <li className='NavigatorGroupItem'>
                            <Link to={'/' + c} children={c}/>
                        </li>)}
                </ul>)
            }
        </div>
    }
}