import * as React from 'react';
import { IImageProps, Image } from './Image';
import './Gallery.css';

interface IGalleryProps
{
    Title: string;
    Genre: string;
    Description: string;
    Images?: IImageProps[];
}

export class Gallery extends React.Component<IGalleryProps>
{
    constructor(props: IGalleryProps)
    {
        super(props);
    }

    render(): React.ReactNode
    {
        return <div className='Gallery'>
            <header className='Header'>
                <h1 className='Title'>{this.props.Title}</h1>
                <p  className='Genre'>{this.props.Genre}</p>
            </header>
            <div className='Content'>
                <div className='Description'>{this.props.Description.split('\n').map(v => <p>{v}</p>)}</div>
                {this.props.Images?.map(i => <Image Path={i.Path} Resolution={i.Resolution} AutoPlay/>)}
            </div>
        </div>
    }

    componentDidUpdate(): void
    {
        let right_area = document.getElementsByClassName("RightArea");
        right_area.length && (right_area[0].scrollTop = 0);
    }
}