import * as React from 'react';
import { Link } from 'react-router-dom';
import { IImageProps, Image } from '../Components/Image';
import './GroupsView.css';

function SplitArray(array: any[], len: number): any[]
{
    let arrays = [];
    for(let i = 0; i < array.length; i += len)
        arrays.push(array.slice(i, i + len));
    return arrays;
}


interface IGroupsViewProps
{
    Name: string;
    Descirption: string;
    Galleries: 
    {
        Name: string;
        Cover: string;
    }[];
}

export class GroupsView extends React.Component<IGroupsViewProps>
{

    constructor(props: IGroupsViewProps)
    {
        super(props);
    }

    public render(): JSX.Element
    {
        return <div className='GroupsView'>
            <header className='Header'>
                <h1 className='Title'>{this.props.Name}</h1>
                <p  className='Description'>{this.props.Descirption}</p>
            </header>
            <div className='Content'>
                <table>
                    <tbody>{
                        SplitArray(this.props.Galleries, 2).map(rows => 
                        <tr>{
                            rows.map((e: any) => 
                                <td>
                                    <Link className='Link' to={`/${e.Name}`}>
                                        <Image Path={e.Cover}/>
                                        <p className='Title'>{e.Name}</p>
                                    </Link>
                                </td>)
                        }</tr>)
                    }</tbody>
                </table>
            </div>
        </div>
    }
}