import * as React from 'react';
import { HashRouter, Navigate, Route, Routes } from 'react-router-dom';
import { IWebConfig, GetConfig } from '../Config/WebConfig';
import { Gallery } from '../Components/Gallery';
import { Navigator } from '../Components/Navigator';
import { GroupsView } from './GroupsView';
import './MainView.css';

interface IMainViewState
{
    IsReady: boolean;
}

export class MainView extends React.Component<{}, IMainViewState>
{
    private Config: IWebConfig;

    constructor(props: {})
    {
        super(props);
        this.state = { IsReady: false };
        (async () =>
        {
            this.Config = await GetConfig();
            this.setState({IsReady: true});
        })();
    }

    public render(): JSX.Element
    {
        
        const get_content = () => <HashRouter>
            <div className='LeftArea'>
                <header onClick={()=> location.hash = '/'}>dj</header>
                <Navigator Contents={this.Config.Groups}/>
            </div>
            <div className='RightArea'>
                <Routes>
                    {this.Config.Groups
                    .map(v =>
                    [
                        { Name: v.Title, Type: 1 },
                        ...v.Galleries.map(u => { return { Name: u, Type: 2 }})
                    ])
                    .reduce((p, v) => [...p, ...v])
                    .map(route => 
                    {
                        if (route.Type == 1)
                        {
                            let group = this.Config.Groups.find(v => v.Title == route.Name);
                            if (!group)
                                return null;
                            let galleries = group.Galleries.map(g =>
                            {
                                return { Name: g, Cover: `/resources/${this.Config.Galleries.find(v => v.Name == g)?.Resources[0]}` }
                            })
                            return galleries && <Route path={'/' + route.Name} element={<GroupsView Name={group.Title} Descirption={group.Description} Galleries={galleries} />}/>
                        }
                        else if (route.Type == 2)
                        {
                            let gallery = this.Config.Galleries.find(v => v.Name == route.Name);
                            return gallery && <Route path={'/' + route.Name} element={<Gallery Title={gallery.Name} Genre={gallery.Genre} Description={gallery.Description} Images={gallery.Resources.map(r => { return {Path: `/resources/${r}`}})}/>} />
                        }
                        return null;
                    })}
                    <Route path="*" element={<Navigate replace to={`/${this.Config.Groups[0].Title}`} />}/>
                </Routes>
            </div>
        </HashRouter>

        return <div className='MainView'>
            <div className='Content'>
                {this.state.IsReady && get_content()}
            </div>
        </div>;
    }
}