export interface IGroup
{
    Title: string;
    Galleries: string[];
    Description: string;
}

export interface IGallery
{
    Name: string;
    Genre: string;
    Description: string;
    Resources: string[];
}

export interface IWebConfig
{
    LogoText: string;
    Groups: IGroup[];
    Galleries: IGallery[];
}


let _WebConfig: IWebConfig = null;
export async function GetConfig(): Promise<IWebConfig>
{
    if (_WebConfig == null)
    {
        let index = await fetch("./index");
        let json = await index.json();
        _WebConfig = json;
    }
    return _WebConfig;
}