import * as React from 'react';
//import './Image.css';



function RandomNumber(start: number, end: number)
{
    return Math.floor(Math.random() * (end + 1 - start) + start);
}

function RandomColor()
{
    let r = RandomNumber(0, 255).toString(16).padStart(2, '0'),
        g = RandomNumber(0, 255).toString(16).padStart(2, '0'),
        b = RandomNumber(0, 255).toString(16).padStart(2, '0');

    return `#${r}${g}${b}`;
    
}

export interface IImageProps
{
    Path?: string;
    Resolution?: number[];
    AutoPlay?: boolean;
}

export class Image extends React.Component<IImageProps>
{
    constructor(props: IImageProps)
    {
        super(props);
    }

    render(): React.ReactNode
    {
        //let resolution = this.props.Resolution || [RandomNumber(3000, 4000), RandomNumber(500, 1000)];
        /*const aspectRatio = `1 / ${(resolution[1] / resolution[0]).toFixed(3)}`;
        const color = RandomColor();*/

        return <div className='Image'>
            {this.props.Path && this.props.Path.includes(".mp4")
            ? <video className='ImageContent' src={`./${this.props.Path}`} autoPlay={this.props.AutoPlay} muted loop></video>
            : <img   className='ImageContent' src={`./${this.props.Path}`}></img>}
        </div>
    }
}


